<template>
  <div class="main" id="diagram">
    <div id="svg" style="width: 100%;height: 100%;padding: 20px 30px 0"></div>
  </div>
</template>

<script>
import Snap from 'snapsvg-cjs'


export default {
  name: "powerSupply.vue",
  data() {
    return {}
  },
  mounted: function () {
    this.loadSvg();
  },
  methods: {
    loadSvg: function () {
      var s = Snap("#svg");
      Snap.load("/images/diantu.svg", function (svg) {
        s.append(svg);
      });
    }
  }
}
</script>

<style scoped>
/deep/ .el-scrollbar__view, .main, .diagram {
  width: 100%;
  height: 100%;
}
</style>
